import React, { useState } from 'react'
import { Container } from 'react-bootstrap';
import Breadcrumbs from '../../../ReusableComponents/breadcrumbs/Breadcrumbs';
import './classification.scss'
import {Link as LinkOfDom}  from 'react-router-dom'
import { Element, Link } from 'react-scroll';
const Classification = () => {
  const [showScrollToTop, setShowScrollToTop] = useState(false);

  var Scroll = require("react-scroll");
  var scroll = Scroll.animateScroll;
  const scrollTop = () => {
    scroll.scrollToTop();
    setShowScrollToTop(false)
  }
  return (
    <Container style={{ backgroundColor: '#fff', padding: '20px 20px', borderRadius: '8px' }}>
      <Breadcrumbs></Breadcrumbs>
      <div className='classification_wrapper'>
        <button className={showScrollToTop ? 'scrollToTopBtn show' : 'scrollToTopBtn'} onClick={scrollTop}><i className='fa-solid fa-chevron-up' /></button>

        <div className="alphabet">
          <span><Link onClick={() => setShowScrollToTop(true)} to='a'></Link></span>
          <span><Link onClick={() => setShowScrollToTop(true)} to='b'></Link></span>
          <span><Link onClick={() => setShowScrollToTop(true)} to='g'></Link></span>
          <span><Link onClick={() => setShowScrollToTop(true)} to='d'></Link></span>
          <span><Link onClick={() => setShowScrollToTop(true)} to='e'></Link></span>
          <span><Link onClick={() => setShowScrollToTop(true)} to='v'></Link></span>
          <span><Link onClick={() => setShowScrollToTop(true)} to='z'></Link></span>
          <span><Link onClick={() => setShowScrollToTop(true)} to='th'></Link></span>
          <span><Link onClick={() => setShowScrollToTop(true)} to='i'></Link></span>
          <span><Link onClick={() => setShowScrollToTop(true)} to='k'></Link></span>
          <span><Link onClick={() => setShowScrollToTop(true)} to='l'></Link></span>
          <span><Link onClick={() => setShowScrollToTop(true)} to='m'></Link></span>
          <span><Link onClick={() => setShowScrollToTop(true)} to='n'></Link></span>
          <span><Link onClick={() => setShowScrollToTop(true)} to='o'></Link></span>
          <span><Link onClick={() => setShowScrollToTop(true)} to='p'></Link></span>
          <span><Link onClick={() => setShowScrollToTop(true)} to='jh'></Link></span>
          <span><Link onClick={() => setShowScrollToTop(true)} to='r'></Link></span>
          <span><Link onClick={() => setShowScrollToTop(true)} to='s'></Link></span>
          <span><Link onClick={() => setShowScrollToTop(true)} to='t'></Link></span>
          <span><Link onClick={() => setShowScrollToTop(true)} to='u'></Link></span>
          <span><Link onClick={() => setShowScrollToTop(true)} to='f'></Link></span>
          <span><Link onClick={() => setShowScrollToTop(true)} to='q'></Link></span>
          <span><Link onClick={() => setShowScrollToTop(true)} to='gh'></Link></span>
          <span><Link onClick={() => setShowScrollToTop(true)} to='y'></Link></span>
          <span><Link onClick={() => setShowScrollToTop(true)} to='sh'></Link></span>
          <span><Link onClick={() => setShowScrollToTop(true)} to='ch'></Link></span>
          <span><Link onClick={() => setShowScrollToTop(true)} to='c'></Link></span>
          <span><Link onClick={() => setShowScrollToTop(true)} to='dz'></Link></span>
          <span><Link onClick={() => setShowScrollToTop(true)} to='w'></Link></span>
          <span><Link onClick={() => setShowScrollToTop(true)} to='ch'></Link></span>
          <span><Link onClick={() => setShowScrollToTop(true)} to='kh'></Link></span>
          <span><Link onClick={() => setShowScrollToTop(true)} to='j'></Link></span>
          <span><Link onClick={() => setShowScrollToTop(true)} to='h'></Link></span>
        </div>
        <div className='profs'>
          <Element name="a">
            <div>
              <h1></h1>
              <li><LinkOfDom to='1'></LinkOfDom></li>
              <li><LinkOfDom to='2'></LinkOfDom></li>
              <li><LinkOfDom to='3'></LinkOfDom></li>
            </div>
          </Element>
          <Element name="b">
            <div>
              <h1></h1>
              <li></li>
              <li> </li>
              <li></li>
            </div>
          </Element>
          <Element name="g">
            <div>
              <h1></h1>
              <li></li>
              <li></li>
              <li> </li>
              <li> </li>
              <li></li>
            </div>
          </Element>
          <Element name="d">
            <div>
              <h1></h1>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
            </div>
          </Element>
          <Element name="e">
            <div>
              <h1></h1>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
            </div>
          </Element>


        </div>

      </div>
    </Container>

  )
}

export default Classification